  position: relative;
}

.switcher-position {
  justify-content: space-between;
  padding-left: 10px;
  padding-right: 10px;
  font-size: 12px;
  display: flex;
}

.slider-btn {
  position: relative;
}

.slider-btn:before {
  content: "";
  background-color: var(--blue);
  height: 26px;
  width: 71px;
  border-radius: 50px;
  margin: auto;
  display: inline-block;
  top: 0;
  bottom: 0;
  box-shadow: 0 0 4px rgba(0, 0, 0, .494);
}

.slider-btn:after {
  content: "";
  background-color: var(--accent-red);
  height: 16px;
  width: 16px;
  border-radius: 50%;
  margin: auto;
  transition: all .4s;
  display: inline-block;
  position: absolute;
  top: -20px;
  bottom: 0;
  left: 5px;
}

.body-light .slider-btn:after {
  background-color: var(--grayish-red);
}

.body-violet .slider-btn:after {
  background-color: var(--pure-cyan);
}

.toggle-radio {
  opacity: 0;
  cursor: pointer;
  z-index: 10;
  position: absolute;
}

.light {
  left: 22px;
}

.violet {
  left: 44px;
}

.basic:checked ~ .slider-btn:after {
  transform: translateX(0);
}

.light:checked ~ .slider-btn:before {
  background-color: var(--light-grey);
}

.light:checked ~ .slider-btn:after {
  transform: translateX(22px);
}

.violet:checked ~ .slider-btn:before {
  background-color: var(--violet);
}

.violet:checked ~ .slider-btn:after {
  transform: translateX(45px);
}

